.menu {
    height: 100vh;
  
    overflow: auto; /* to scroll the menu if needed */

    &__content {
        @include contentPaddingInline();

        display: flex;
        flex-direction: column;
        gap: 50px;   
    }

    &__items {
        display: flex;
        flex-direction: column;
        gap: 20px;
        align-items: center;
    }

    &__link{
        color:#2A86E5;
        font-weight: 500;
        font-size: 18px;
        line-height: 25px;
    }

    // &__newlist {
    //     margin-bottom: 60px;
    // }

    &__newitems {
        display: flex;
        flex-direction: column;
        gap: 20px;
        align-items: center;
    }

    &__newlink {
        display: flex;
        align-items: center;
        color: #707070;
        font-weight: 500px;
        font-size: 18px;
        line-height: 18px;
    }

}

.form {

    &__label {
        position: relative;
    }

    &__input {
        border-radius: 40px;
        border: none;
        width: 100%;
        height: 40px;
        background-color: #EEEEEE;
        padding-left: 20px;
        font-size: 16px;
        line-height: 20px;
    }
}
